<template>
	<div>
		<div>中国最美的100个地方 看你适合哪个地方</div>
		<div class="placeBox">
			<span v-for="i in placeArr">{{i}}</span>
		</div>
		<button @click="choosePlace" class="btn">哪个地方更适合你</button>
		<input class="input" v-model="placeName" type="" placeholder="美丽的城市在等你" name="" id="" value="" />
		<!--<a target="_blank" :href="['https://baike.baidu.com/?tn=monline_3_dg&ie=utf-8&wd=' + placeName]">{{placeName}}</a>-->
	</div>
</template>

<script>
	export default {
		data() {
			return {
				placeName:"",
				placeArr: [
					"白鹿恐龙园",
					"西双版纳热带雨林",
					"蜀南竹海",
					"呼伦贝尔",
					"神农架",
					"丰宁坝上草原",
					"西溪",
					"金沙江虎跳峡",
					"长江三峡",
					"鸣沙山·月牙泉",
					"雅鲁藏布大峡谷",
					"石林",
					"香格里拉",
					"九寨沟",
					"黄龙",
					"稻城",
					"桃花源 最美江湖",
					"杭州西湖",
					"青海湖",
					"喀纳斯湖",
					"羊卓雍错",
					"纳木错",
					"泸沽湖",
					"天山天池",
					"长白山天池",
					"黄果树瀑布",
					"壶口瀑布",
					"三江并流",
					"日月潭",
					"洱海",
					"滇池",
					"太湖",
					"洞庭湖",
					"钱塘江",
					"京杭大运河",
					"五大连池",
					"千岛湖",
					"亚龙湾",
					"天涯海角",
					"鼓浪屿",
					"漓江 最美名山",
					"黄山",
					"华山",
					"泰山",
					"嵩山",
					"衡山",
					"恒山",
					"庐山",
					"青城山",
					"武夷山",
					"阿里山",
					"峨眉山",
					"普陀山",
					"五台山",
					"梵净山",
					"三清山",
					"玉龙雪山",
					"梅里雪山",
					"珠穆朗玛峰 最美古镇",
					"丽江大研古镇",
					"平遥古城",
					"凤凰",
					"乌镇",
					"周庄",
					"大理古城",
					"婺源",
					"西塘",
					"甪直",
					"南浔",
					"坝美村",
					"爨底下村 最美古迹",
					"北京故宫",
					"天坛",
					"颐和园",
					"明十三陵",
					"承德避暑山庄",
					"福建土楼",
					"苏州园林",
					"都江堰",
					"乐山大佛",
					"大足石刻",
					"龙门石窟",
					"云冈石窟",
					"莫高窟",
					"万里长城",
					"布达拉宫 最美城市",
					"什刹海",
					"三里屯",
					"南锣鼓巷",
					"上海外滩",
					"新天地",
					"上海浦东",
					"维多利亚港",
					"澳门历史城区",
					"哈尔滨中央大街",
					"成都锦里",
					"阳朔",
					"青岛",
					"大连",
					"杭州清河坊",
					"南京秦淮河"
				]
			}
		},
		methods: {
			choosePlace:function(){
				clearInterval(times);
				var $time = 10;
				var $this = this;
				var $span = document.getElementsByTagName("span");
				var times = setInterval(function(){
					var placeClass = document.querySelector(".chooseClass");
					if(placeClass){
						placeClass.classList.remove("chooseClass");
					}
					var num = parseInt(99*Math.random());
					$span[num].classList.add("chooseClass");
					$time--;
					if($time <= 0){
						clearInterval(times);
						$this.placeName = $span[num].innerHTML;
					}
				},200);
			}
		}
	}
</script>

<style>
	.placeBox {
		border: 1px solid #e6e6e6;
		border-radius: 4px;
		text-align: left;
		padding: 20px 12px;
	}
	.chooseClass{
		background: #007aff;
		color: #FFFFFF;
	}
	.placeBox span {
		display: inline-block;
		padding: 10px;
		border: 1px solid #E6E6E6;
		margin-left: 4px;
		margin-top: 4px;
		transition: all .5s;
	}
	.btn{
		padding: 10px;
		border: none;
		outline: none;
		background: red;
		border-radius: 4px;
		color: white;
		margin-top: 20px;
	}
	.input{
		height: 34px;
		border: 1px solid #E6E6E6;
		border-radius: 4px;
		margin-top: 20px;
	}
</style>


